  setFileValController(base64: string, fcName: string) {
    if (base64) {
      let file: IFileUploadObject = {
        fileExt: "png",
        fileName: fcName + ".png",
        isImage: true,
        file: base64,
        status: "N",
        imageUrl: "data:image/png;base64," + base64
      }
      if (!this.stepperOptions.group.controls[fcName]) {
        this.stepperOptions.group.addControl(fcName, new FormControl())
      }
      if (!Array.isArray(this.stepperOptions.requestObject[fcName + "_uploadedFiles"])) {
        this.stepperOptions.requestObject[fcName + "_uploadedFiles"] = [];
      }
      this.stepperOptions.group.controls[fcName].setValue(file);
      this.stepperOptions.group.controls[fcName][ConstantCommon.CONTROLLER_CALLED_AFTER_CHANGE] = true;
      this.stepperOptions.group.controls[fcName][ConstantCommon.CONTROLLER_CALL_VALIDATE_SERVICE] = false;
      // this.stepperOptions.requestObject[fcName] = file;
      this.stepperOptions.requestObject[fcName + "_uploadedFiles"].push(file);
    }
  }


